.form {
  background-color: $sapphire;
  color: $color-default-white;
  padding: 56px 170px 70px 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  & form {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: end;
  }
}

.form__heading {
  @include style-reset();
  font-family: $ff-additional;
  font-weight: 800;
  font-size: 54px;
  line-height: 84px;
  letter-spacing: 0.03em;
  margin-bottom: 20px;
  padding: 0 36px 0 0;
}

.form__contact {
  display: flex;
  gap: 30px;
  width: 100%;
  justify-content: end;

  & label {
    max-width: 190px;
    width: 100%;
  }

  & input {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $light-gray;
    width: 100%;
  }
}
